import { Component} from '@angular/core';
import { NavController, NavParams, ViewController, App, AlertController } from 'ionic-angular';
import { AppService, AppConfig } from '../../../app/app.service';
@Component({
  selector: 'add-account',
  templateUrl: 'add-account.html'
})
export class AddAccount {
  salesName: string = '';
  cellphone: string = '';
  IDcard: string = '';
  wechatOpenid: any;
  userCurrent: any;
  accountContent: Boolean = false;//html
  noBind: Boolean = true;//html
  boundWechat: Boolean;//
  requestDefeat: Boolean = false;
  loadingShow: Boolean = false;
  load: any = {}; 
  userId: number;
  isName: Boolean = false;//
  isPhone: Boolean = false;//
  isIDCard: Boolean = false;//
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl : ViewController,
    public appService: AppService,
    public app: App,
    public alertCtrl: AlertController
  ) {
    
  }
  bindWX() {
    if (this.salesName != "" && this.cellphone.length == 11 && this.IdentityCodeValid(this.IDcard)) {
      this.isName = false;
      this.isPhone = false;
      this.isIDCard = false;
      this.loadingShow = true;
      let editCurrentUrl = AppConfig.API.current;
      let editParameters = {
        salesName: this.salesName,
        cellphone: this.cellphone,
        idcard: this.IDcard
      }
      //
      this.appService.httpPut(editCurrentUrl, editParameters).then(data => {
        if (data.type == "success") {
          this.loadingShow = false;
          let redirectUri = `https://mobile.${AppConfig.mainUrl}`;
          let encodeUrl = encodeURIComponent(redirectUri);
          let getCodeUrl = `${AppConfig.API.connect}?appid=${AppConfig.appID}&redirect_uri=${encodeUrl}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`;
          window.location.href = getCodeUrl;
        }
      }).catch(error => {
        this.appService.getToken(error, () => {
          this.bindWX();
        });
        this.loadingShow = false;
        console.log(error);
        this.appService.toast('', 1000, 'middle');
      });
    }else if (this.salesName == "") {
      this.isName = true;
      this.isPhone = false;
      this.isIDCard = false;
    }else if (this.cellphone.length != 11) {
      this.isName = false;
      this.isPhone = true;
      this.isIDCard = false;
    }else if (!this.IdentityCodeValid(this.IDcard)) {
      this.isName = false;
      this.isPhone = false;
      this.isIDCard = true;
    }
    
  }
  //
  editCurrent() {
    if (this.salesName != "" && this.cellphone.length == 11 && this.IdentityCodeValid(this.IDcard)) {
      let confirm = this.alertCtrl.create({
        title: '',
        buttons: [
          {
            text: '',
            handler: () => {
              
            }
          },
          {
            text: '',
            handler: () => {
              this.isName = false;
              this.isPhone = false;
              this.isIDCard = false;
              this.loadingShow = true;
              let editCurrentUrl = AppConfig.API.current;
              let editParameters = {
                salesName: this.salesName,
                cellphone: this.cellphone,
                idcard: this.IDcard
              }
              //
              this.appService.httpPut(editCurrentUrl, editParameters).then(data => {
                if (data.type == "success") {
                  this.loadingShow = false;
                  this.getCurrent();
                  this.appService.toast('', 1000, 'middle');
                }
              }).catch(error => {
                this.appService.getToken(error, () => {
                  this.editCurrent();
                });
                this.loadingShow = false;
                console.log(error);
                this.appService.toast('', 1000, 'middle');
              });
            }
          }
        ]
      });
      confirm.present();
    }else if (this.salesName == "") {
      this.isName = true;
      this.isPhone = false;
      this.isIDCard = false;
    }else if (this.cellphone.length != 11) {
      this.isName = false;
      this.isPhone = true;
      this.isIDCard = false;
    }else if (!this.IdentityCodeValid(this.IDcard)) {
      this.isName = false;
      this.isPhone = false;
      this.isIDCard = true;
    }
  }
  //
  getCurrent() {
    this.load = AppConfig.load;
    this.loadingShow = true;
    this.accountContent = false;
    let getCurrentUrl = AppConfig.API.current;
    this.appService.httpGet(getCurrentUrl)
      .then( data => {
        this.loadingShow = false;
        this.requestDefeat = false;
        this.accountContent = true;
        this.boundWechat = data.boundWechat;
        this.salesName = data.salesName;
        this.cellphone = data.cellphone;
        this.IDcard = data.idcard;
        if (this.boundWechat) {
          this.noBind = false;
        }else {
          this.noBind = true;
        }
      })
      .catch(error => {
        this.appService.getToken(error, () => {
          this.getCurrent();
        });
        console.log(error);
        this.loadingShow = false;
        this.requestDefeat = true;
        this.accountContent = false;
      });
  }
  ionViewDidEnter() {
    //
    if (window.location.search && window.location.search.split("?")[1].indexOf("code") > -1) {
      this.accountContent = false;
      let loading = this.appService.loading();
      loading.present();
      let code = window.location.search.split("?")[1].split("&")[0].split("=")[1];
      let getTokenUrl = `${AppConfig.API.callback}?code=${code}&state=STATE`;
      this.appService.httpGet(getTokenUrl).then(data => {
        if (data.type == "success") {
          this.appService.setItem("stopReturn", "have");
          loading.dismiss();
          this.noBind = false;
          this.getCurrent();
        }
      }).catch(error => {
        loading.dismiss();
        this.accountContent = false;
        this.noBind = true;
        console.log(error);
        this.appService.toast('', 1000, 'middle');
      })
    }else {
      this.getCurrent();
    }
  }

  //
  IdentityCodeValid(code) { 
    var city={11:"",12:"",13:"",14:"",15:"",21:"",22:"",23:" ",31:"",32:"",33:"",34:"",35:"",36:"",37:"",41:"",42:" ",43:"",44:"",45:"",46:"",50:"",51:"",52:"",53:"",54:" ",61:"",62:"",63:"",64:"",65:"",71:"",81:"",82:"",91:" "};
    var pass= true;
    if(!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)){
      pass = false;
    }else if(!city[code.substr(0,2)]){
      pass = false;
    }else{
      //18
      if(code.length == 18){
        code = code.split('');
        //(aiWi)(mod 11)
        //
        var factor = [ 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2 ];
        //
        var parity = [ 1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2 ];
        var sum = 0;
        var ai = 0;
        var wi = 0;
        for (var i = 0; i < 17; i++)
        {
          ai = code[i];
          wi = factor[i];
          sum += ai * wi;
        }
        if(parity[sum % 11] != code[17]){
          pass =false;
        }
      }
    }
    return pass;
  }
}



