import { Component, ViewChild } from '@angular/core';
import { NavController, ModalController, AlertController, Content } from 'ionic-angular';
import { HandleExpressgift } from '../handle-expressgift/handle-expressgift';
import { AppService, AppConfig } from '../../app/app.service';
@Component({
  selector: 'unhandle-expressgift',
  templateUrl: 'unhandle-expressgift.html'
})
export class UnhandleExpressgift {
	@ViewChild(Content) content: Content;
	unhandleExpressGiftArray: any;
	start: number = 0;
	limit: number = 10;
  showNoMore: Boolean = false;
	noData: Boolean;
	up: Boolean;//
	down: Boolean;//
	load: any = {};
  loadingShow: Boolean = true;
	constructor(public navCtrl: NavController, 
		public modalCtrl: ModalController, 
		public alertCtrl: AlertController,
		public appService: AppService,
	) {
		//
		this.down = true;
		this.up = false;
		this.unhandleExpressGiftArray = [];
		this.load = AppConfig.load;
		this.getUnhandleExpressGiftList();
	}
	getUnhandleExpressGiftList() {
		let url = `${AppConfig.API.getGiftList}?type=1&start=${this.start}&limit=${this.limit}`;//brandshopSeq=${this.brandshopSeqId}
		this.appService.httpGet(url).then( data => {
			this.loadingShow = false;
			console.log(data)
			if (data.count == 0) {
				//
				this.noData = true;
			}else {
				this.noData = false;
				if( this.start < data.count ) {
					if (this.up) {
						this.unhandleExpressGiftArray.push(...data.data);
						this.start += this.limit;
					}else if (this.down){
						this.unhandleExpressGiftArray = data.data;
						this.start += this.limit;
						this.content.scrollTo(0,0,0); 
					}
				}else {
					this.showNoMore = true;
				}
			}
		}).catch(error => {
			this.appService.getToken(error, () => {
				this.getUnhandleExpressGiftList();
			});
			this.loadingShow = false;
			console.log(error);
			this.appService.toast('', 1000, 'middle');
		});
  }
	goExpressgift() {
		const orderModal = this.modalCtrl.create(HandleExpressgift);
		orderModal.onDidDismiss(() => {
		// 
			this.start = 0;
			this.down = true;
			this.up = false;
			this.getUnhandleExpressGiftList();
		})
		orderModal.present();
	}
	sendProduct(index) {
		let alert = this.alertCtrl.create({
			message: '',
			inputs: [
		      {
		        name: 'companyName',
		        type: 'text',
		        placeholder: ''
		      },{
		        name: 'orderNum',
		        type: 'text',
		        placeholder: ''
		      }
			],
			buttons: [
			  {
			    text: '',
			    handler: () => {
			      //
			    }
			  },
			  {
			    text: '',
			    handler: data => {
						if (data.companyName != "" && data.orderNum != ""){
							let body = {
								memberGiftAccountSeq: this.unhandleExpressGiftArray[index].memberGiftAccountSeq,
								expressCompany: data.companyName,
								expressNo: data.orderNum
							}
							let loading = this.appService.loading();
							loading.present();
							let url = AppConfig.API.confirmExpressInfo;
							this.appService.httpPost(url, body).then( data => {
								if (data.type == "success") {
									this.start = 0;
									this.down = true;
									this.up = false;
									loading.dismiss();
									this.getUnhandleExpressGiftList();
								}
							}).catch(error => {
								loading.dismiss();
								console.log(error);
								this.appService.toast('', 1000, 'middle');
							});
						}else {
							this.appService.toast('', 1000, 'middle');
						}
			    }
			  }
			]
		});
		alert.present();
	}

	// 
	refreshGetUnhandleExpressGiftList(refresher) {
	this.start = 0;
	this.down = true;
	this.up = false;
	let url = `${AppConfig.API.getGiftList}?type=1&start=${this.start}&limit=${this.limit}`;
    this.appService.httpGet(url).then( data => {
			refresher.complete();
			if (data.count == 0) {
        //
        this.noData = true;
      }else {
        this.noData = false;
				if (data.data.length != 0) {
					this.unhandleExpressGiftArray = data.data;
					this.start += this.limit;
				}else {
					this.showNoMore = true;
				}
			}
    }).catch(error => {
			this.appService.getToken(error, () => {
				this.refreshGetUnhandleExpressGiftList(refresher);
			});
      refresher.complete();
      console.log(error);
      this.appService.toast('', 1000, 'middle');
    });
	}
	infiniteGetUnhandleExpressGiftList(infiniteScroll) {
	// 
		this.down = false;
		this.up = true;
		let url = `${AppConfig.API.getGiftList}?type=1&start=${this.start}&limit=${this.limit}`;
		this.appService.httpGet(url).then( data => {
			infiniteScroll.complete();
			if (data.count == 0) {
        //
        this.noData = true;
      }else {
        this.noData = false;
				if (data.data.length != 0) {
					this.unhandleExpressGiftArray.push(...data.data);
					this.start += this.limit;
				}else {
					this.showNoMore = true;
				}
			}
		}).catch(error => {
			this.appService.getToken(error, () => {
				this.infiniteGetUnhandleExpressGiftList(infiniteScroll);
			});
			infiniteScroll.complete();
			console.log(error);
			this.appService.toast('', 1000, 'middle');
		});
	}
}



